import { useState,useEffect } from "react";

function Playground() {
  const [text, setText] = useState("a");
  useEffect(() => {
    function onTimeout() {
      console.log("⏰ " + text);
    }
    console.log('🔵 Schedule "' + text + '" log');
    const clrT = setTimeout(onTimeout, 3000);
    return () => {
      console.log('🟡 Cancel "' + text + '" log');
      clearTimeout(clrT);
    };
  }, [text]);
  return (
    <>
      <label>
        What to log: <input value={text} onChange={(e) => setText(e.target.value)} />
      </label>
      <h1>{text}</h1>
    </>
  );
}
export default function EfffectTime() {
  const [show, setShow] = useState(false);
  return (
    <div>
      <button onClick={() => setShow(!show)}>{show ? "Unmount" : "Mount"} the component</button>
      {show && <hr />}
      {show && <Playground />}
    </div>
  );
}
